import React, { useState, useEffect } from 'react';
import MainCard from '../../../../ui-component/cards/MainCard';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { withStyles, useTheme } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';

import { deleteFocusSkill, getAllFocusSkill } from '../../../../store/actions/FocusSkillAdminActions';
import { IconSearch } from '@tabler/icons';
import EditIcon from '@material-ui/icons/Edit';
import SubjectIcon from '@material-ui/icons/Subject';
import useUserRole from '../../../../hooks/useUserRole';
import useStyles from './styles';
import AddFocusSkill from '../focus-skill/add-focus-skill/index';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import ConfirmDialog from '../../../../ui-component/ConfirmDialog/ConfirmDialog';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function FocusSkill() {
    useUserRole({ roleList: ['ADMIN', 'TEACHER', 'SUPER_ADMIN'] });
    const [openAddFocusSkill, setOpenAddFocusSkill] = useState(false);
    const [isFocusSkillUpdateData, setFocusSkillUpdateData] = useState(null);
    const classes = useStyles();
    const [confirmDialog, setConfirmDialog] = useState({
        isOpen: false,
        title: '',
        subTitle: ''
    });
    const dispatch = useDispatch();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const [page, setPage] = useState(0);

    const [rowsPerPage, setRowsPerPage] = useState(10);

    const [isSearch, setSearch] = useState('');

    const { allFocusSkills } = useSelector((state) => state?.focusSkill);
    const newData = allFocusSkills;
    const totalCount = allFocusSkills?.totalElements;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    const handleAddFocusSkill = (id) => {
        setOpenAddFocusSkill(true);
        setFocusSkillUpdateData(id);
    };

    const handleDeleteFocusSkill = (id) => {
        const payload = {
            deleted: true
        };
        dispatch(
            deleteFocusSkill(payload, id, () => {
                dispatch(getAllFocusSkill(rowsPerPage, isSearch ? isSearch : '', page + 1));
            })
        );
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        });
    };

    const handleSearch = (event) => {
        setSearch(event.target.value);
    };

    useEffect(() => {
        dispatch(getAllFocusSkill(rowsPerPage, isSearch ? isSearch : '', page + 1));
    }, [rowsPerPage, isSearch, page]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; Focus Skills </span>
                </div>

                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3} style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Grid item xs={12} sm={6} md={6}>
                            <div>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    onChange={(event) => handleSearch(event)}
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                            </div>
                        </Grid>
                        {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                            <Grid item xs={12} sm={6} md={6}>
                                <div className={classes.btn}>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        className={classes.btnAddPlanOptions}
                                        // component={Link}
                                        onClick={() => {
                                            setOpenAddFocusSkill(true);
                                            setFocusSkillUpdateData(null);
                                        }}
                                        // className={classes.btnAddSubCategories}
                                        // to="/admin/add-focus-skill"
                                        style={{ cursor: 'pointer' }}
                                    >
                                        Add Focus Skill
                                    </Button>
                                </div>
                            </Grid>
                        )}
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Image </TableCell>

                                    <TableCell align="center"> Title </TableCell>

                                    {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                                        <TableCell align="right"> Action </TableCell>
                                    )}
                                </StyledTableRow>
                            </TableHead>
                            {!newData ? (
                                <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                            ) : (
                                <TableBody>
                                    {newData?.content?.map((row, index) => (
                                        <StyledTableRow key={index}>
                                            <TableCell>
                                                <img src={row?.imageUrl} alt="icon" width="60px" />
                                            </TableCell>
                                            <TableCell align="center"> {row?.title} </TableCell>

                                            <TableCell align="right">
                                                {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                                                    <Tooltip title="Update Skill">
                                                        <IconButton onClick={() => handleAddFocusSkill(row)}>
                                                            <EditIcon color="primary" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                )}
                                                {loggedUserProfile?.role === 'SUPER_ADMIN' && (
                                                    <>
                                                        <Tooltip title="Delete Focus Skill">
                                                            <IconButton
                                                                onClick={() => {
                                                                    setConfirmDialog({
                                                                        isOpen: true,
                                                                        title: 'Delete Focus Skill',
                                                                        subTitle: 'Are you sure you want to delete this Focus Skill?',
                                                                        onConfirm: () => {
                                                                            handleDeleteFocusSkill(row?.id);
                                                                        }
                                                                    });
                                                                }}
                                                            >
                                                                <DeleteForeverIcon color="error" style={{ cursor: 'pointer' }} />
                                                            </IconButton>
                                                        </Tooltip>
                                                    </>
                                                )}
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[10, 15, 25]}
                        component="div"
                        count={totalCount || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>

                {openAddFocusSkill && (
                    <AddFocusSkill isOpen={openAddFocusSkill} setOpen={setOpenAddFocusSkill} isEditData={isFocusSkillUpdateData} />
                )}
                {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}
            </MainCard>
        </>
    );
}
